import {applyMiddleware, combineReducers, createStore} from "redux";
import {profileReduer} from "./profileReduer";
import {dialogReduer} from "./dialogReduer";
import {participantReduer} from "./participantReduer";
import {authReduer} from "./authReduer";
import thunk from "redux-thunk";
import {reducer as formReducer} from 'redux-form'

const commonReducer = combineReducers({
    profileState: profileReduer,
    dialogState: dialogReduer,
    participantState: participantReduer,
    auth: authReduer,
    form:formReducer
})

export const store = createStore(commonReducer,applyMiddleware(thunk))

export type StateType = ReturnType<typeof commonReducer>

// @ts-ignore
window.store = store